<!-- 信维——产能推移看板 -->
<template>
  <div class="kanban">
    <!-- <div class="titleHead">
      <p id="pHead"></p>
      <p id="time">{{ time }}</p>
    </div> -->
    <search-bar @search="loadData" :searchData="searchData" :searchOptions="searchOptions" @getGroupSucceed="loadData"></search-bar>
    <div class="kanbanContent">
      <div class="top_content">
        <div class="tabs_chart">
          <el-radio-group v-model="capacityType" size="small" @input="radioChange">
            <el-radio-button label="0">产能推移图</el-radio-button>
            <el-radio-button label="1" >产能对比图</el-radio-button>
          </el-radio-group>
        </div>
        <card-module title="产能推移图" cardBg="cardBg_large" titleBg="cardTitle_large" width="100%" height="100%"
          titleWidth="70%" @exportExcel="exportCapacity">
          <div style="width: 100%; height: 100%" ref="capacity" v-if="capacityType === '0'"></div>
          <div style="width: 100%; height: 100%" ref="capacityComparison" v-else></div>
        </card-module>
      </div>
      <div class="bottom_content">
        <card-module title="告警时长" width="50%" height="100%" titleWidth="80%" @exportExcel="exportWarn(warnData)">
          <div style="width: 100%; height: 100%" ref="downTime"></div>
        </card-module>
        <card-module title="良品率" width="49%" height="100%" titleWidth="80%" @exportExcel="exportYield(yieldData)">
          <div style="width: 100%; height: 100%" ref="YR"></div>
        </card-module>
      </div>
    </div>
  </div>
</template>

<script>
// import $ from 'jquery';
// import vueSeamlessScroll from 'vue-seamless-scroll';
import $ from 'jquery';
import { fontSize, parseTime } from '@/utils/common';
import { KANBAN_CONFIG } from '@/utils/ajaxConfigure';
import CardModule from '@/components/common/kanban/CardModule';
import searchBar from './components/searchBar.vue';
import { exportJson2Excel } from '@/utils/exportExcel';
import {
  prodChartYield,
  listWarnStatisticsChart,
  prodChartGroup,
  yieldComparisonChart
} from '@/api/xwReport';
import { deviceGetPageDeviceComplexByConditionAction } from '@/api/management';
import { getSearchToday, getTodayFirstDay, getTodayLastDay } from '@/utils/publicFunction2';
import { showWarningMessage } from '@/utils/message';
import { showErrorNotify } from '@/utils/notify';
import {
  STATUS_CODE,
  TITLE
} from '@/utils/constantsFile';
export default {
  name: 'kanban_xw_capacity',
  components: {
    // vueSeamlessScroll,
    CardModule,
    searchBar
  },
  data() {
    return {
      title: '',
      groupId: 0,
      time: '',
      dataTime: '',
      capacityType: '0', // 0: 产能推移图， 1： 产量对比图
      searchData: {
        // groupValue: [1, 2, 3, '00000190_00000005'],
        groupValue: '00000190_00000005',
        date: [new Date(getSearchToday(6) + ' 00:00:00'), new Date(getSearchToday(0) + ' 23:59:59')],
        timeKeyEnum: 'DAY'
      },
      searchOptions: [
        // {
        //   type: 'cascader',
        //   key: 'groupValue',
        //   name: '车间'
        // },
        {
          type: 'select',
          key: 'groupValue',
          options: []
        },
        {
          type: 'daterange',
          key: 'date',
          name: '时间',
          options: [
            {
              label: '小时',
              value: 'LESSON'
            },
            {
              label: '天',
              value: 'DAY'
            },
            {
              label: '年',
              value: 'MONTH',
              disabled: false
            }
          ]
        }
      ],
      kanbanConfig: { title: '安灯看板', key: '', time: 15000 },
      stateEnum: {
        yellow_off: '停用',
        red_e: '异常',
        blue_m: '保养',
        green_w: '待料',
        white_m: '维护',
        green_p: '生产'
      },
      fontStateEnum: {
        停用: 'yellow_off_font',
        异常: 'red_e_font',
        保养: 'blue_m_font',
        待料: 'green_w_font',
        维护: 'white_m_font',
        生产: 'green_p_font'
      },
      deviceList: [],
      coatingData: {},
      barChartInfo: {
        current: 1,
        size: 4
      },
      maintainAnalyse: {
        current: 1,
        size: 4
      },
      timer: null,
      timer2: null,
      yieldXData: [],
      yieldData: [],
      warnXData: [],
      warnData: [],
      prodChartXData: [],
      prodChartData: [],
      prodChartDevices: []
    };
  },
  mounted() {
    document.documentElement.style.fontSize = this.fontSize() + 'px';
    window.onresize = () => {
      document.documentElement.style.fontSize = this.fontSize() + 'px';
    };
  },
  created() {
    var that = this;
    if (KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)]) {
      that.kanbanConfig =
        KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)];
    }
    this.findSpecificDevice();
    // this.getDataTime();
  },
  destroyed() {
    this.timer && setInterval(this.timer);
    this.timer2 && setInterval(this.timer2);
  },
  methods: {
    exportCapacity() {
      const multiHeader = [];
      const header = ['', ...this.prodChartXData];
      const data = this.prodChartDevices.map(key => {
        return [key, ...this.prodChartData[key].map(item => item.actualQty)];
      });
      console.log(data);
      exportJson2Excel({
        multiHeader,
        header,
        data,
        filename: '产能推移图',
        merges: []
      });
    },
    exportWarn(seriesData) {
      const multiHeader = [];
      const data = [seriesData];
      exportJson2Excel({
        multiHeader,
        header: this.warnXData,
        data,
        filename: '告警时长',
        merges: []
      });
    },
    exportYield(seriesData) {
      const multiHeader = [];
      const data = [seriesData];
      exportJson2Excel({
        multiHeader,
        header: this.yieldXData,
        data,
        filename: '良品率',
        merges: []
      });
    },
    // 查询具体设备
    async findSpecificDevice() {
      try {
        const params = {
          startTime: getTodayFirstDay(),
          endTime: getTodayLastDay(),
          current: 1,
          size: 99999999
        };
        var conditionJson = {
          conditionJson1: [
            { column: 'isCollect', type: 'eq', ptype: 'string', value: 1, operator: 1 }
          ],
          operatorGroup: 1,
          conditionJson2: []
        };
        const res = await deviceGetPageDeviceComplexByConditionAction(params, conditionJson);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.searchOptions[0].options = res.rows.map(item => ({
            label: item.deviceName,
            value: item.deviceCode
          }));
          const params = new URLSearchParams(location.search);
          if (params.has('deviceCode')) {
            this.searchData.groupValue = params.get('deviceCode');
          } else {
            this.searchData.groupValue = res.rows[0].deviceCode;
          }
          this.loadData();
        } else {
          showWarningMessage(res.message);
        }
      } catch (error) {
        showErrorNotify(TITLE.NOTIFY, error);
      } finally {
      }
    },
    getDataTime() {
      this.dataTime = new Date(
        $.ajax({ type: 'HEAD', async: false }).getResponseHeader('Date')
      ).getTime();
      this.toGetTime();
    },
    toGetTime() {
      setInterval(() => {
        var date = new Date(this.dataTime);
        var y = date.getFullYear();
        var M =
          date.getMonth() < 9
            ? '0' + (date.getMonth() + 1)
            : date.getMonth() + 1;
        var d = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
        var h = date.getHours() < 10 ? '0' + date.getHours() : date.getHours();
        var m =
          date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes();
        var s =
          date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds();
        this.time =
          y + ' 年 ' + M + ' 月 ' + d + ' 日   ' + h + ':' + m + ':' + s;
        this.dataTime += 1000;
      }, 1000);
    },
    fontSize(num = 10) {
      const clientWidth =
        window.innerWidth ||
        document.documentElement.clientWidth ||
        document.body.clientWidth;
      if (!clientWidth) return;
      const fontSize = clientWidth / 1920;
      return num * fontSize;
    },
    showLogo() {
      return 'logo_' + this.kanbanConfig.logo;
    },
    getUrlKeyMain(name, url) {
      // eslint-disable-next-line no-sparse-arrays
      return (
        decodeURIComponent(
          (new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(
            url
          ) || ['', ''])[1].replace(/\+/g, '%20')
        ) || null
      );
    },
    async getProdChartGroup() {
      try {
        const { timeKeyEnum, date, groupValue } = this.searchData;
        const param = {
          timeKeyEnum
        };
        if (date) {
          if (timeKeyEnum === 'MONTH') {
            param.startTime = parseTime(date);
            param.endTime = parseTime(date);
          } else if (timeKeyEnum === 'LESSON') {
            param.startTime = parseTime(date[0]);
            param.endTime = parseTime(date[1]);
          } else if (timeKeyEnum === 'DAY') {
            const newDate = date[1];
            newDate.setHours(23);
            newDate.setMinutes(59);
            newDate.setSeconds(59);
            param.startTime = parseTime(date[0]);
            param.endTime = parseTime(newDate);
          }
        } else {
          param.timeKeyEnum = 'DAY';
          param.startTime = getSearchToday(6) + ' 00:00:00';
          param.endTime = getSearchToday(0) + ' 23:59:59';
        }
        if (groupValue) {
          // var str = groupValue[groupValue.length - 1];
          var str = groupValue;
          if (isNaN(str)) {
            param.deviceCode = str;
          } else {
            param.groupId = str;
          }
        }
        const res = await prodChartGroup(param, {});
        this.prodChartDevices = Object.keys(res.rows);
        this.prodChartXData = [];
        this.prodChartData = res.rows;
        if (this.prodChartDevices.length > 0) {
          this.prodChartXData = this.prodChartData[this.prodChartDevices[0]].map(item => item.key);
        }
        this.drawCapacity(this.prodChartDevices, this.prodChartXData, this.prodChartData);
      } catch (error) {
        console.log(error);
      }
    },
    radioChange(val) {
      console.log(val);
      if (val === '1') {
        this.getYieldComparisonChart();
        // this.searchOptions[1].options[2].disabled = true;
        // this.$forceUpdate();
      } else if (val === '0') {
        this.getProdChartGroup();
        // this.searchOptions[1].options[2].disabled = false;
        // this.$forceUpdate();
      }
    },
    // 产能对比图数据
    async getYieldComparisonChart() {
      try {
        const { timeKeyEnum, date, groupValue } = this.searchData;
        const param = {
          timeKeyEnum,
          deviceCode: groupValue
        };
        if (date) {
          if (timeKeyEnum === 'MONTH') {
            param.startTime = parseTime(date);
            param.endTime = parseTime(date);
          } else if (timeKeyEnum === 'LESSON') {
            param.startTime = parseTime(date[0]);
            param.endTime = parseTime(date[1]);
          } else if (timeKeyEnum === 'DAY') {
            const newDate = date[1];
            newDate.setHours(23);
            newDate.setMinutes(59);
            newDate.setSeconds(59);
            param.startTime = parseTime(date[0]);
            param.endTime = parseTime(newDate);
          }
        } else {
          param.timeKeyEnum = 'DAY';
          param.startTime = getSearchToday(6) + ' 00:00:00';
          param.endTime = getSearchToday(0) + ' 23:59:59';
        }
        const res = await yieldComparisonChart(param, {});
        const XData = [];
        const actualQty = [];
        const planQty = [];
        res.rows.forEach(item => {
          XData.push(item.key);
          actualQty.push(item.actualQty);
          planQty.push(item.planQty);
        });
        this.drawCapacityComparison(XData, actualQty, planQty);
        console.log(res);
      } catch (error) {
        console.log(error);
      }
    },
    async getWarnData() {
      try {
        const { timeKeyEnum, date, groupValue } = this.searchData;
        const param = {
          timeKeyEnum
        };
        if (date) {
          if (timeKeyEnum === 'MONTH') {
            param.startTime = parseTime(date);
            param.endTime = parseTime(date);
          } else if (timeKeyEnum === 'LESSON') {
            param.startTime = parseTime(date[0]);
            param.endTime = parseTime(date[1]);
          } else if (timeKeyEnum === 'DAY') {
            const newDate = date[1];
            newDate.setHours(23);
            newDate.setMinutes(59);
            newDate.setSeconds(59);
            param.startTime = parseTime(date[0]);
            param.endTime = parseTime(newDate);
          }
        } else {
          param.timeKeyEnum = 'DAY';
          param.startTime = getSearchToday(6) + ' 00:00:00';
          param.endTime = getSearchToday(0) + ' 23:59:59';
        }
        if (groupValue) {
          // var str = groupValue[groupValue.length - 1];
          var str = groupValue;
          if (isNaN(str)) {
            param.deviceCode = str;
          } else {
            param.groupId = str;
          }
        }
        const res = await listWarnStatisticsChart(param, {});
        console.log(res);
        this.warnXData = res.rows.map(item => item.key);
        this.warnData = res.rows.map(item => item.radio);
        this.drawDownTime(this.warnXData, this.warnData);
      } catch (error) {
        console.log(error);
      }
    },
    async getYieldChartData() {
      try {
        const { timeKeyEnum, date, groupValue } = this.searchData;
        const param = {
          timeKeyEnum
        };
        if (date) {
          if (timeKeyEnum === 'MONTH') {
            param.startTime = parseTime(date);
            param.endTime = parseTime(date);
          } else if (timeKeyEnum === 'LESSON') {
            param.startTime = parseTime(date[0]);
            param.endTime = parseTime(date[1]);
          } else if (timeKeyEnum === 'DAY') {
            const newDate = date[1];
            newDate.setHours(23);
            newDate.setMinutes(59);
            newDate.setSeconds(59);
            param.startTime = parseTime(date[0]);
            param.endTime = parseTime(newDate);
          }
        } else {
          param.timeKeyEnum = 'DAY';
          param.startTime = getSearchToday(6) + ' 00:00:00';
          param.endTime = getSearchToday(0) + ' 23:59:59';
        }
        if (groupValue) {
          // var str = groupValue[groupValue.length - 1];
          var str = groupValue;
          if (isNaN(str)) {
            param.deviceCode = str;
          } else {
            param.groupId = str;
          }
        }
        const res = await prodChartYield(param, {});
        console.log(res);
        this.yieldXData = res.rows.map(item => item.key);
        this.yieldData = res.rows.map(item =>
          parseFloat(item.yield.toFixed(2))
        );
        var jhll = res.rows.map(item => {
          return item.planYield;
        });
        this.drawYR(this.yieldXData, this.yieldData, jhll);
      } catch (error) {
        console.log(error);
      }
    },
    drawDownTime(xData, seriesData) {
      const barChart = this.$echarts.init(this.$refs.downTime);
      const option = {
        // color: EQUIPMENT_STATUS_REPORT.COLOR,
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        // barMaxWidth: '60%',
        grid: {
          top: '12%',
          left: '3%',
          right: '8%',
          bottom: '8%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            data: xData,
            axisTick: {
              alignWithLabel: true
            },
            axisLine: {
              lineStyle: {
                color: '#929EE6'
              }
            },
            axisLabel: {
              fontSize: fontSize(14),
              color: '#D8D8D8'
            },
            nameTextStyle: {
              fontSize: fontSize(14)
            },
            name: '日期',
            nameGap: '5'
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '分钟',
            minInterval: 1,
            axisLine: {
              lineStyle: {
                color: '#929EE6'
              }
            },
            axisLabel: {
              fontSize: fontSize(14),
              color: '#D8D8D8'
            },
            nameTextStyle: {
              fontSize: fontSize(14)
            },
            splitLine: {
              show: false
            }
          }
        ],
        series: [
          {
            type: 'bar',
            barWidth: '35%',
            // itemStyle: {
            //   color: '#09DBA0'
            // },
            itemStyle: {
              color: '#EE8231'
              // borderWidth: 1,
              // borderColor: '#ffffff',
              // borderRadius: 10
            },
            label: {
              show: true,
              position: 'top',
              color: '#ffffff'
            },
            data: seriesData
          }
        ]
      };
      barChart.setOption(option, true);
      window.addEventListener('resize', () => {
        barChart.resize();
      });
    },
    //
    drawYR(xData, seriesData, planYield) {
      const barChart = this.$echarts.init(this.$refs.YR);
      const option = {
        // color: EQUIPMENT_STATUS_REPORT.COLOR,
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        // barMaxWidth: '60%',
        grid: {
          top: '12%',
          left: '3%',
          right: '8%',
          bottom: '8%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            data: xData,
            axisTick: {
              alignWithLabel: true
            },
            axisLine: {
              lineStyle: {
                color: '#929EE6'
              }
            },
            axisLabel: {
              fontSize: fontSize(14),
              color: '#D8D8D8'
            },
            nameTextStyle: {
              fontSize: fontSize(14)
            },
            name: '日期',
            nameGap: '5'
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '百分比',
            // minInterval: 1,
            // splitNumber: 0.2,
            axisLine: {
              lineStyle: {
                color: '#929EE6'
              }
            },
            axisLabel: {
              fontSize: fontSize(14),
              color: '#D8D8D8'
            },
            nameTextStyle: {
              fontSize: fontSize(14)
            },
            splitLine: {
              show: false
            }
          }
        ],
        series: [
          {
            type: 'bar',
            barWidth: '50%',
            itemStyle: {
              color: '#09DBA0'
            },
            label: {
              show: true,
              position: 'top',
              color: '#ffffff'
            },
            data: seriesData
          },
          {
            name: '',
            type: 'line',
            barWidth: '35%',
            itemStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [{
                  offset: 0,
                  color: '#058cff'
                }, {
                  offset: 1,
                  color: '#058cff'
                }])
              }
            },
            data: planYield
          }
          // {
          //   name: '计划良率%',
          //   type: 'line',
          //   yAxisIndex: 1,
          //   // smooth: true,
          //   showAllSymbol: true,
          //   symbol: 'circle',
          //   symbolSize: 10,
          //   itemStyle: {
          //     color: '#058cff'
          //   },
          //   lineStyle: {
          //     color: '#058cff'
          //   },
          //   areaStyle: {
          //     color: 'rgba(5,140,255, 0.2)'
          //   },
          //   data: planYield
          // }
        ]
      };
      barChart.setOption(option, true);
      window.addEventListener('resize', () => {
        barChart.resize();
      });
    },
    // 产能推移图
    drawCapacity(devices, XData, data) {
      const Capacity = this.$echarts.init(this.$refs.capacity);
      const colorList = ['#9E87FF', '#73DDFF', '#fe9a8b', '#F56948', '#9E87FF'];
      const option = {
        grid: {
          top: '12%',
          left: '3%',
          right: '4%',
          bottom: '8%',
          containLabel: true
        },
        legend: {
          icon: 'circle',
          top: '2%',
          right: '5%',
          itemWidth: fontSize(15),
          itemGap: fontSize(40),
          textStyle: {
            color: '#ffffff'
          }
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: 'line' // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        xAxis: [
          {
            type: 'category',
            data: XData,
            boundaryGap: false,
            axisTick: {
              alignWithLabel: true
            },
            axisLine: {
              lineStyle: {
                color: '#929EE6'
              }
            },
            axisLabel: {
              // interval: 0,
              fontSize: fontSize(14),
              color: '#D8D8D8'
            },
            nameTextStyle: {
              fontSize: fontSize(14)
            },
            name: ''
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '产量',
            // minInterval: 1,
            // splitNumber: 0.2,
            min: function(value) {
              return value.min - 10;
            },
            axisLine: {
              lineStyle: {
                color: '#929EE6'
              }
            },
            axisLabel: {
              fontSize: fontSize(14),
              color: '#D8D8D8'
            },
            nameTextStyle: {
              fontSize: fontSize(14)
            },
            splitLine: {
              show: false
            }
          },
          {
            type: 'value',
            name: '',
            // minInterval: 1,
            // splitNumber: 0.2,
            axisLine: {
              lineStyle: {
                color: '#929EE6'
              }
            },
            axisLabel: {
              fontSize: fontSize(14),
              color: '#D8D8D8'
            },
            nameTextStyle: {
              fontSize: fontSize(14)
            },
            splitLine: {
              show: false
            }
          }
        ],
        series: devices.map((key, index) => ({
          name: key,
          type: 'line',
          data: data[key].map(item => item.actualQty),
          symbolSize: 1,
          symbol: 'circle',
          smooth: true,
          yAxisIndex: 0,
          showSymbol: false,
          lineStyle: {
            width: fontSize(5),
            // color: new this.$echarts.graphic.LinearGradient(0, 1, 0, 0, [
            //   {
            //     offset: 0,
            //     color: '#9effff'
            //   },
            //   {
            //     offset: 1,
            //     color: '#9E87FF'
            //   }
            // ]),
            shadowColor: 'rgba(158,135,255, 0.3)',
            shadowBlur: 10,
            shadowOffsetY: 20
          },
          itemStyle: {
            normal: {
              color: colorList[index],
              borderColor: colorList[index]
            }
          }
        }))
      };
      Capacity.setOption(option, true);
      window.addEventListener('resize', () => {
        Capacity.resize();
      });
    },
    drawCapacityComparison(XData, actualQty, planQty) {
      const CapacityComparison = this.$echarts.init(this.$refs.capacityComparison);
      // const colorList = ['#9E87FF', '#73DDFF', '#fe9a8b', '#F56948', '#9E87FF'];
      const option = {
        grid: {
          top: '12%',
          left: '3%',
          right: '4%',
          bottom: '8%',
          containLabel: true
        },
        legend: {
          icon: 'circle',
          top: '2%',
          right: '5%',
          itemWidth: fontSize(15),
          itemGap: fontSize(40),
          textStyle: {
            color: '#ffffff'
          }
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: 'line' // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        xAxis: [
          {
            type: 'category',
            data: XData,
            boundaryGap: false,
            axisTick: {
              alignWithLabel: true
            },
            axisLine: {
              lineStyle: {
                color: '#929EE6'
              }
            },
            axisLabel: {
              // interval: 0,
              fontSize: fontSize(14),
              color: '#D8D8D8'
            },
            nameTextStyle: {
              fontSize: fontSize(14)
            },
            name: ''
          }
        ],
        yAxis: [
          {
            type: 'value',
            name: '产量',
            // minInterval: 1,
            // splitNumber: 0.2,
            min: function(value) {
              return value.min - 10;
            },
            axisLine: {
              lineStyle: {
                color: '#929EE6'
              }
            },
            axisLabel: {
              fontSize: fontSize(14),
              color: '#D8D8D8'
            },
            nameTextStyle: {
              fontSize: fontSize(14)
            },
            splitLine: {
              show: false
            }
          }
        ],
        series: [
          {
            name: '实际产量',
            type: 'line',
            data: actualQty,
            symbolSize: 1,
            symbol: 'circle',
            smooth: true,
            yAxisIndex: 0,
            showSymbol: false,
            lineStyle: {
              width: fontSize(5),
              shadowColor: 'rgba(158,135,255, 0.3)',
              shadowBlur: 10,
              shadowOffsetY: 20
            },
            itemStyle: {
              normal: {
                color: '#9E87FF',
                borderColor: '#9E87FF'
              }
            }
          },
          {
            name: '计划产量',
            type: 'line',
            data: planQty,
            symbolSize: 1,
            symbol: 'circle',
            smooth: true,
            yAxisIndex: 0,
            showSymbol: false,
            lineStyle: {
              width: fontSize(5),
              type: 'dashed',
              shadowColor: 'rgba(158,135,255, 0.3)',
              shadowBlur: 10,
              shadowOffsetY: 20
            },
            itemStyle: {
              normal: {
                color: '#73DDFF'
                // borderColor: '#9E87FF'
              }
            }
          }
        ]
      };
      CapacityComparison.setOption(option, true);
      window.addEventListener('resize', () => {
        CapacityComparison.resize();
      });
    },
    loadData() {
      this.getYieldChartData();
      this.getWarnData();
      // this.getProdChartGroup();
      if (this.capacityType === '1') {
        this.getYieldComparisonChart();
      } else if (this.capacityType === '0') {
        this.getProdChartGroup();
      }
    }
  }
};
</script>

